import React from 'react';
import './FaceRecognition.css';

const FaceRecognition = ({imageUrl, box})=> {
	return (
		<div className="center" style={{ display: 'inline-block', position: 'relative' }}>
			<img className='bird'id='inputimage'alt='' src= {imageUrl} width='500px' height='auto'/>
			<div 
				className='bounding-box'
				style={{top: box.topRow, right: box.rightCol, bottom: box.bottomRow, left: box.leftCol}}></div>
		</div>
	);
}


export default FaceRecognition;